import React, { useRef, useState, useEffect } from "react";
import './App.css';

import Grid from '@mui/material/Grid2';
import Button from '@mui/material/Button';

import MaxHeightTextarea from "./Components/TextArea/TextArea"

import '@fontsource/roboto/300.css';
import '@fontsource/roboto/400.css';
import '@fontsource/roboto/500.css';
import '@fontsource/roboto/700.css';

import PlayerAndRecorder from "./Components/PlayerAndRecorder/PlayerAndRecorder";

function App() {
    const playerRef = useRef(null);

    const [videos, setVideos] = useState([]);
    const [selectedVideo, setSelectedVideo] = useState(null);
    const [selectedVideoName, setSelectedVideoName] = useState(""); // Track the name of the selected video
    const [selectedVideoIndex, setSelectedVideoIndex] = useState(0);
    const [directoryHandle, setDirectoryHandle] = useState(null);

    const [annotationState, setAnnotationState] = useState({
        to_annotate: 0,
        finished: 0
    })

    const handleDirectorySelection = async () => {
        try {
          // Open directory picker
            const newDirectoryHandle = await window.showDirectoryPicker();
            setDirectoryHandle(newDirectoryHandle);

            const subdirectoryHandle = await newDirectoryHandle.getDirectoryHandle("AudioRecordings", { create: true });
            
            const videoFiles = [];
            let existingJsonCounter = 0;
          
            // Iterate through files in the directory
            for await (const [name, handle] of newDirectoryHandle.entries()) {
                if (handle.kind === 'file' && /\.(mp4|webm|ogg|mkv)$/i.test(name)) {
                    let videoId = name.substring(0, name.lastIndexOf("."));
                    const jsonFileName = `${videoId}.json`;
                    let jsonExists = false;

                    try {
                        await subdirectoryHandle.getFileHandle(jsonFileName);
                        jsonExists = true; // JSON file exists
                    } catch (e) {
                        // If file does not exist, an error will be thrown, handle it gracefully
                        jsonExists = false;
                    }

                    if (jsonExists) {
                        existingJsonCounter++;
                    } else {
                        // If JSON file doesn't exist, add the video file to videoFiles
                        const file = await handle.getFile();
                        const url = URL.createObjectURL(file);
                        videoFiles.push({ name, url });
                    }
                }
            }
            setVideos(videoFiles);
            if (videoFiles.length > 0) {
                setSelectedVideo(videoFiles[0].url);
                setSelectedVideoName(videoFiles[0].name); // Set the initial video name
                console.log("Selected video:", videoFiles[0].name); // Print the initial video name
            }
            setAnnotationState((prev) => ({ ...prev, to_annotate: videoFiles.length, finished: existingJsonCounter }));
        } catch (error) {
            console.error("Error accessing directory:", error);
        }
    };

    const handleVideoChange = () => {
        console.log("comments:", textareaRef.current.value);
        saveJsonData(true, textareaRef.current.value);
        const nextIndex = selectedVideoIndex + 1;
        setSelectedVideoIndex(nextIndex); // Move to the next video
        setAnnotationState((prev) => ({ ...prev, to_annotate: prev.to_annotate - 1, finished: prev.finished + 1 }));
        if (selectedVideoIndex < videos.length - 1) {
            setSelectedVideo(videos[nextIndex].url);
            setSelectedVideoName(videos[nextIndex].name)
            console.log("Playing next video:", videos[nextIndex].name);
        } else {
            console.log("End of playlist.");
        }
    };

    const textareaRef = useRef();

    // Function to save the JSON data to a file
    const saveJsonData = async (withComments, commentsToSave) => {
        try {
            // Create or open the file where the JSON data will be stored
            const subdirectoryHandle = await directoryHandle.getDirectoryHandle("AudioRecordings", { create: true });
            const videoId = selectedVideoName.substring(0, selectedVideoName.lastIndexOf("."));
            const jsonFileHandle = await subdirectoryHandle.getFileHandle(`${videoId}.json`, { create: true });
            const writable = await jsonFileHandle.createWritable();

            // Write the videoRecordings object as a JSON string to the file
            let data = null;
            if (withComments) {
                data = {'recordings': videoRecordings, 'comments': commentsToSave};
            } else {
                data = {'recordings': videoRecordings};
            }
            await writable.write(JSON.stringify(data, null, 2)); // Pretty-print JSON
            await writable.close();

            console.log("JSON data saved.");
        } catch (error) {
            console.error("Error saving JSON data:", error);
        }
    };

    const [videoRecordings, setVideoRecordings] = useState([]); // Object to store video ID, time_step, and file paths

    return (
        <Grid container rowSpacing={1}  justifyContent="center" alignItems="center" direction="column">
            <h1>Video Annotation App</h1>
            <Grid item container spacing={2} justifyContent="center" alignItems="center" direction="row">
                <div style={{ marginBottom: '0px', padding: '10px', border: '1px solid #ccc', borderRadius: '8px', backgroundColor: '#f9f9f9' }}>
                    <h2>Script</h2>
                    <ol>
                        <li>I am evaluating the last <b>[X seconds]</b> of the video.</li>
                        <li>I have <b>[POSITIVE / NEGATIVE]</b> feedback to give.</li>
                        <li>The activity in question is <b>[DESCRIPTION HERE]</b>.</li>
                        <li>The category I am commenting on is <b>[GIVE META COMPETENCY CATEGORY HERE OR a category outside of the meta competencies]</b>.</li>
                        <li>My assessment is <b>[WHAT ABOUT THE PERFORMANCE WAS LACKING / DONE WELL]</b>.</li>
                        <li>I flagged this performance because <b>[WHAT IS THE THING YOU ARE TRYING TO AVOID BY GIVING THE FEEDBACK]</b>.</li>
                        <ul>
                            <li>
                                Negative Feedback reasons: Safety, Efficiency, poor form (give a meaningful reason), etc.
                            </li>
                            <li>
                                Positive Feedback reasons: Prevents injury here or there, etc.
                            </li>
                        </ul>
                        <li>(Negative feedback only) The way it can be improved is <b>[GIVE HOW IT CAN BE IMPROVED]</b>.</li>
                    </ol>
                </div>
                <Grid item>
                    <PlayerAndRecorder 
                        video={selectedVideo} 
                        videoName={selectedVideoName}
                        playerRef={playerRef}
                        onVideoChange={handleVideoChange}
                        nextVideoDisabled={selectedVideoIndex === videos.length || selectedVideoName === ""}
                        directoryHandle={directoryHandle}
                        saveJsonData={saveJsonData}
                        videoRecordings={videoRecordings}
                        setVideoRecordings={setVideoRecordings}>
                    </PlayerAndRecorder>
                </Grid>
                <Grid item container spacing={5} justifyContent="left" alignItems="space-evenly" direction="column">
                    <Button variant="contained" onClick={handleDirectorySelection}>
                        Select Video Directory
                    </Button>
                    <span>
                        <b>Current video:</b> {selectedVideoName || "None selected"}
                    </span>
                    <span>
                        <b>Videos to annotate:</b> {annotationState.to_annotate}
                    </span>
                    <span>
                        <b>Videos annotated:</b> {annotationState.finished}
                    </span>
                    <Grid item container spacing={2} direction="column">
                        <span>Comments:</span>
                        <MaxHeightTextarea
                            textareaRef={textareaRef}></MaxHeightTextarea>
                    </Grid>
                </Grid>
            </Grid>
        </Grid>
    );
}

export default App;