import React from "react";
import ReactPlayer from "react-player";
import "./VideoPlayer.css";

export default function VideoPlayer({src, isPlaying, playerRef, isVideoLoaded, onLoadedData, handlePlay, handlePause, handleProgress, handleVideoEnd}) {

    return (
        <div className="player">
        { src !== '' ? (
            <div style={{ opacity: isVideoLoaded ? 1 : 0 }}>
            <ReactPlayer
                ref={playerRef}
                url={src}
                playing={isPlaying}
                controls={true}
                loop={false}
                muted={true}
                playsinline={true}
                onReady={onLoadedData}
                onPlay={handlePlay}
                onPause={handlePause}
                onProgress={handleProgress}
                onEnded={handleVideoEnd}
                width="100%"
                height="100%"
                // onSeek={handlePlayerSeek}
            />
            </div>
        ) : (
            <div> No video selected </div>
        )}
        </div>
    );
}