import React, { useState, useRef, useEffect } from "react";
import { styled } from '@mui/material/styles';
import Paper from '@mui/material/Paper';
import VideoPlayer from "../VideoPlayer/VideoPlayer";

import { useAudioRecorder } from 'react-audio-voice-recorder';
import { LiveAudioVisualizer } from 'react-audio-visualize';

// import { FFmpeg } from '@ffmpeg/ffmpeg';

import './PlayerAndRecorder.css'

import Button from '@mui/material/Button';
import Grid from '@mui/material/Grid2';

const Item = styled(Paper)(({ theme }) => ({
    backgroundColor: '#fff',
    ...theme.typography.body2,
    padding: theme.spacing(1),
    textAlign: 'center',
    color: theme.palette.text.secondary,
    ...theme.applyStyles('dark', {
      backgroundColor: '#1A2027',
    }),
  }));

export default function PlayerAndRecorder({video, videoName, playerRef, onVideoChange, nextVideoDisabled, directoryHandle, saveJsonData, videoRecordings, setVideoRecordings}) {
    
    const [isNarrating, setIsNarrating] = useState(false);
    const [numRecording, setNumRecording] = useState(0);

    const [playerstate, setPlayerState] = useState({
        playing: false,
        played:0,
        seeking: false,
        loaded: false
    });

    const recorderControls = useAudioRecorder()

    useEffect(() => {
        if (recorderControls.recordingBlob) {
            saveAudio(recorderControls.recordingBlob);
        }
    }, [recorderControls.recordingBlob]); // This effect will run when recordingBlob is updated


    const onLoadedData = async () => {
        setPlayerState((prev) => ({ ...prev, playing: false, played :0, seeking: false, loaded: true }));
        setNumRecording(0);
        setVideoRecordings([]);
        setIsNarrating(false);
    };

    const handleProgress = (state) => {
        setPlayerState({...playerstate, ...state});
    }

    const handlePause = () => {
        setPlayerState((prev) => ({ ...prev, playing: false }));
    };

    const handlePlay = () => {
        console.log("Video is playing")
        setPlayerState((prev) => ({ ...prev, playing: true }));
    }

    // const saveAudio = async (blob) => {
    //     if (blob) {
    //         const downloadblob = await convertToDownloadFileExtension(blob);
    //         console.log("save audio");
    //         const subdirectoryHandle = await directoryHandle.getDirectoryHandle("AudioRecordings", { create: true });
    //         const videoId = videoName.substring(0, videoName.lastIndexOf("."));
    //         const fileName = `${videoId}_${numRecording}.mp3`; // Give the file a name
    //         const fileHandle = await subdirectoryHandle.getFileHandle(fileName, { create: true });
    //         const writable = await fileHandle.createWritable();

    //         // Write the audio blob data
    //         await writable.write(downloadblob);
    //         // await writable.write(blob);
    //         await writable.close();
    //         setNumRecording(numRecording + 1);
    //         // Update the videoRecordings object with the new data
    //         videoRecordings.push({'timestamp': playerRef.current.getCurrentTime(), 'filename': fileName})
    //         // Save the JSON file with the updated data
    //         await saveJsonData();
    //     }
    // };

    // const { FFmpeg } = require('@ffmpeg/ffmpeg');

    const saveAudio = async (blob) => {
        // const ffmpeg = new FFmpeg({ log: true });
        // const ffmpeg = new FFmpeg({log: true, corePath: "./node_modules/@ffmpeg/core/dist/umd/ffmpeg-core.js",});
        // await ffmpeg.load();
    
        try {
            // console.log("Converting WebM to MP3...");

            // console.log("ffmpeg", ffmpeg);
            
            // // Load the WebM blob into FFmpeg
            // ffmpeg.writeFile('input.webm', new Uint8Array(await blob.arrayBuffer()));
    
            // // Run the conversion command
            // await ffmpeg.run('-i', 'input.webm', 'output.mp3');
    
            // // Retrieve the MP3 file from FFmpeg
            // const mp3Data = ffmpeg.readFile('output.mp3');
    
            // // Create an MP3 Blob
            // const mp3Blob = new Blob([mp3Data.buffer], { type: 'audio/mp3' });
    
            // Save the MP3 blob
            const subdirectoryHandle = await directoryHandle.getDirectoryHandle("AudioRecordings", { create: true });
            const videoId = videoName.substring(0, videoName.lastIndexOf("."));
            const fileName = `${videoId}_${numRecording}.webm`; // Use MP3 extension
            // const fileName = `${videoId}_${numRecording}.mp3`; // Use MP3 extension
            const fileHandle = await subdirectoryHandle.getFileHandle(fileName, { create: true });
            const writable = await fileHandle.createWritable();
    
            // await writable.write(mp3Blob);
            await writable.write(blob);
            await writable.close();
    
            console.log("MP3 file saved successfully!");
            setNumRecording(numRecording + 1);
    
            // Update video recordings metadata
            videoRecordings.push({
                timestamp: playerRef.current.getCurrentTime(),
                filename: fileName,
            });
    
            // Save the updated JSON metadata
            await saveJsonData(false, "");
        } catch (error) {
            console.error("Error converting WebM to MP3:", error);
        }
    };

    const handleNarrate = async () => {
        setIsNarrating((prev) => !prev);
    
        if (isNarrating) {
            recorderControls.stopRecording();
        } else {
            recorderControls.startRecording();
        }
    
        console.log(isNarrating ? "Stopped Narration" : "Started Narration");
    };

    useEffect(() => {
        console.log("playerstate.loaded has been updated:", playerstate.loaded);
    }, [playerstate.loaded]); // Runs only when playerstate.loaded changes

    return (
        <Grid container spacing={2} justifyContent="center" alignItems="center" direction="column" >
            <Grid item>
                <Item>
                    {video ? (
                        <VideoPlayer 
                        src={video} 
                        isPlaying={playerstate.playing}
                        playerRef={playerRef}
                        isVideoLoaded={playerstate.loaded}
                        onLoadedData={onLoadedData}
                        handlePlay={handlePlay}
                        handlePause={handlePause}
                        handleProgress={handleProgress}></VideoPlayer>
                    ) : (
                        <div class="box">
                            <h2>No video selected</h2>
                        </div>
                    )
                    };
                </Item>
            </Grid>

            <Grid
                item
                container
                direction="row"
                justifyContent="space-between"
                alignItems="center"
                sx={{ width: '100%' }}
            >
                <Grid item>
                    <Button
                        variant="contained"
                        onClick={handleNarrate}
                        disabled={playerstate.playing || !playerstate.loaded}
                        sx={{
                            backgroundColor: isNarrating ? "#f44336" : "primary", // Change color dynamically
                            "&:hover": {
                                backgroundColor: isNarrating ? "darkred" : "darkblue",
                            },
                        }}
                        >
                        {isNarrating ? "Stop Narration" : "Narrate"}
                    </Button>
                </Grid>
                {recorderControls.mediaRecorder && (
                    <Grid item>
                        <Item>
                        <LiveAudioVisualizer
                            mediaRecorder={recorderControls.mediaRecorder}
                            width={200}
                            height={75}
                        />
                        <span>{`${recorderControls.recordingTime}s`}</span>
                        </Item>
                    </Grid>
                )}
                <Grid item>
                    <Button variant="contained" onClick={onVideoChange} disabled={nextVideoDisabled}>
                        Next Video
                    </Button>
                </Grid>
            </Grid>
        </Grid>
    );
}